// 基础颜色统一配置
$head-theme: "light"; // 选择 light 或 blue
$headerHeight: 60px;

$base-color: #399CFF; // 导航主⾊ 导航⽂字 重要按钮 链接⽂本
$base-hover: #61B0FF; // 主色hover
$bask-dark: #0D90FC;
$chart-first: #88c4ff; // 图表颜色
$chart-second: #b0d7ff; // 图表颜色
$bask-tips-border: #d7ebff; // 提示框边框
$base-card: #e9f5ff; // 卡片，信息提示窗背景
$base-color-blue: #399CFF; // 蓝色主题的导航主⾊ 导航⽂字 重要按钮 链接⽂本

$primary-color: #3fc6ef; // 次要按钮
$primary-hover: #63D6FA; // hover

$info-color: #40C6EF; // 次要按钮
$info-hover: #66D1F2; // hover
$info-border: #d8f4ff; // 边框颜色
$info-back: #ebf9ff; // 背景颜色

$success-color: #3de586; // 操作成功 谨慎使⽤ 成功信息
$success-hover: #59D58F; // hover
$success-border: #DEF6E8; // 边框颜色
$success-back: #EEFAF3; // 背景颜色

$warn-color: #FEA30D; // 警告，谨慎使用
$warn-hover: #F8CC64; // hover
$warn-border: #FFEDCF; // 边框颜色
$warn-back: #FEF5E6; // 背景颜色

$error-color: #ED3F3F; // 操作警告 信息提醒 失败信息
$error-hover: #F39082; // hover
$error-border: #FBD9D9; // 边框颜色
$error-back: #FDEBEB; // 背景颜色

$cancel-color: #C9C9C9; // 取消选择 边框颜⾊
$cancel-hover: #A9A9A9; // hover

$nav-color: #90BFE1; // 导航hover

$sup-color: #F4F9FA; // 辅色

$font-color: #282A2B; // 主要⽂字 标题

$font-second: #5F6568; // 常规文字 正文

$font-third: #888E94; // 次要文字

$border-color: #DADEE2; // 边框

$Dividing-line-color: #E7EBEE; // 分割线

$background-color: #EFF2F5; // 背景

$white-color: #fff; // 白色

$auxiliary-color: #F7F8F9; // 辅助背景









































































































































































































































































































































































































































































































































































































































































.home-wrapper {
    padding: 20px 10px;
    .bk-grid-row {
        .bk-grid-col {
            .content {
                background: #fff;
            }
        }
    }
    .common-container {
        width: 100%;
        height: 100px;
        padding: 15px 20px !important;
        box-sizing: border-box;
        background: #fff;
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
        border-radius: 2px;
        &:hover {
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
        }
    }
    .content-wrapper {
        padding: 20px;
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
        border-radius: 2px;
        .ip-used-percent {
            width: 100%;
            height: 240px;
            box-sizing: border-box;
            color: #63656E;
            letter-spacing: 0;
            padding-bottom: 10px;
            border-bottom: 1px solid #DDE4EB;
            h2 {
                margin: 0 0 15px 0;
                font-size: 14px;
                line-height: 22px;
                font-weight: 600;
            }
            ul {
                li {
                    font-size: 12px;
                    margin-bottom: 20px;
                    vertical-align: middle;
                    height: 18px;
                    line-height: 18px;
                    cursor: pointer;
                    span {
                        display: inline-block;
                        position: relative;
                        vertical-align: middle;
                        text-align: center;
                        width: 18px;
                        height: 18px;
                        em {
                            display: block;
                            font-style: normal;
                            color: #fff;
                            position: absolute;
                            top: 0;
                            left: 31.5%;
                            font-size: 12px;
                            line-height: 16px;
                        }
                    }
                    i {
                        font-size: 18px;
                        display: inline-block;
                        &.radius {
                            width: 15px;
                            height: 15px;
                            background: #699DF4;
                            border-radius: 50%;
                        }
                    }
                    b {
                        float: right;
                        font-weight: normal;
                        color: #63656E;
                    }
                    &.orange {
                        em {
                            left: 30%;
                        }
                        i {
                            color: #FF9C01;
                        }
                        b {
                            color: #FF9C01;
                        }
                    }
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
            }
            .hover-color {
                li:hover {
                    color: #699DF4;
                }
            }
            &.ip-net-used {
                li {
                    cursor: inherit;
                }
            }
        }
        .ip-used-ul {
            width: 100%;
            height: 175px;
            margin: 0 auto;
            overflow: hidden;
            padding-right: 6px;
            overflow-y: scroll;
            li {
                font-size: 12px;
                margin-bottom: 20px;
                vertical-align: middle;
                height: 18px;
                line-height: 18px;
                span {
                    display: inline-block;
                    position: relative;
                    vertical-align: middle;
                    text-align: center;
                    width: 18px;
                    height: 18px;
                    em {
                        display: block;
                        font-style: normal;
                        color: #fff;
                        position: absolute;
                        top: 0;
                        left: 33%;
                        font-size: 12px;
                        line-height: 18px;
                    }
                }
                i {
                    font-size: 18px;
                    display: inline-block;
                    &.radius {
                        width: 15px;
                        height: 15px;
                        background: #699DF4;
                        border-radius: 50%;
                    }
                }
                b {
                    float: right;
                    font-weight: normal;
                }
                &.orange {
                    em {
                        left: 30%;
                    }
                    i {
                        color: #FF9C01;
                    }
                    b {
                        color: #FF9C01;
                    }
                }
            }
            &::-webkit-scrollbar {
                /*滚动条整体样式*/
                width: 6px;  /*高宽分别对应横竖滚动条的尺寸*/
                height: 3px;
                border: none;
            }
            &::-webkit-scrollbar-thumb {
                /*滚动条里面小方块*/
                border-radius: 30px;
                background: transparent;
            }
            &::-webkit-scrollbar-track {
                /*滚动条里面轨道*/
                border-radius: 30px;
                background: transparent;
            }
            &:hover {
                &::-webkit-scrollbar-thumb {
                    background: rgba(186, 198, 204, .6);
                    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
                }
            }
        }
    }
    .used-count {
        width: 100%;
        height: 105px;
        display: flex;
        padding: 20px 10px 0 10px;
        .number {
            flex: 1;
            h2 {
                margin: 0;
                color: #63656E;
                font-size: 24px;
                line-height: 36px;
                span {
                    font-size: 16px;
                    font-weight: normal;
                }
            }
            p {
                color: #979BA5;
                margin: 5px 0;
            }
        }
        .sharp {
            width: 60px;
            height: 60px;
            line-height: 74px;
            text-align: center;
            background-color: #E1ECFF;
            clip-path: polygon(45% 1.33975%, 46.5798% 0.60307%, 48.26352% 0.15192%, 50% 0%, 51.73648% 0.15192%, 53.4202% 0.60307%, 55% 1.33975%, 89.64102% 21.33975%, 91.06889% 22.33956%, 92.30146% 23.57212%, 93.30127% 25%, 94.03794% 26.5798%, 94.48909% 28.26352%, 94.64102% 30%, 94.64102% 70%, 94.48909% 71.73648%, 94.03794% 73.4202%, 93.30127% 75%, 92.30146% 76.42788%, 91.06889% 77.66044%, 89.64102% 78.66025%, 55% 98.66025%, 53.4202% 99.39693%, 51.73648% 99.84808%, 50% 100%, 48.26352% 99.84808%, 46.5798% 99.39693%, 45% 98.66025%, 10.35898% 78.66025%, 8.93111% 77.66044%, 7.69854% 76.42788%, 6.69873% 75%, 5.96206% 73.4202%, 5.51091% 71.73648%, 5.35898% 70%, 5.35898% 30%, 5.51091% 28.26352%, 5.96206% 26.5798%, 6.69873% 25%, 7.69854% 23.57212%, 8.93111% 22.33956%, 10.35898% 21.33975%);
            &.red {
                background: #FFDDDD;
                margin-top: 8px;
            }
        }
        &:hover {
            .sharp {
                -webkit-animation: tada .6s .1s ease both;
                -moz-animation: tada .6s .1s ease both;
                @-webkit-keyframes tada {
                    0% {
                        -webkit-transform: scale(1);
                    }
                    10%, 20% {
                        -webkit-transform: scale(0.9) rotate(-3deg);
                    }
                    30%, 50%, 70%, 90% {
                        -webkit-transform: scale(1.1) rotate(3deg);
                    }
                    40%, 60%, 80% {
                        -webkit-transform: scale(1.1) rotate(-3deg);
                    }
                    100% {
                        -webkit-transform: scale(1) rotate(0);
                    }
                }
            }
        }
    }
    .distribute-solution {
        margin-top: 20px;
        color: #63656E;
        font-size: 14px;
        height: 260px;
        h2 {
            margin: 0 0 15px 0;
            font-size: 14px;
            line-height: 22px;
            font-weight: 600;
        }
        #echarts {
            width: 70%;
            height: 190px;
        }
        .echarts-right-length {
            height: 120px;
            margin-top: 50px;
            .length-title {
                width: 40%;
                display: inline-block;
                position: relative;
                span::before {
                    content: " ";
                    position: absolute;
                    left: -14px;
                    top: 10px;
                    width: 10px;
                    height: 10px;
                    border-radius: 5px;
                }
                .allocated::before {
                    background-color: #7FABF4;
                }
                .unallocated::before {
                    background-color: #7ED4B2;
                }
                .to-recycled::before {
                    background-color: #E9B76E;
                }
                .ecycled::before {
                    background-color: #9DB1CE;
                }
                .icon-grey::before {
                    background-color: #dcdee5;
                }
            }
            .length-count {
                width: 40%;
                display: inline-block;
                text-align: right;
                font-weight: bold;
            }
            span {
                font-size: 12px;
                line-height: 28px;
                cursor: pointer;
            }
            .color-grey {
                color: #dcdee5;
            }
        }
    }
    .bottom-content {
        margin-top: 20px;
        padding: 20px;
        height: 400px;
        background: #fff;
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
        border-radius: 2px;
        h2 {
            margin: 0 0 15px 0;
            font-size: 14px;
            line-height: 22px;
            font-weight: 600;
            color: #63656E;
            .select-wrapper {
                float: right;
                margin-top: -5px;
            }
        }
        #lineCharts {
            width: 100%;
            height: 280px;
        }
        .header {
            display: flex;
            color: #63656E;
            h2 {
                flex: 1;
            }
            ul {
                margin-right: 10px;
                li {
                    float: left;
                    list-style: none;
                    padding: 3px 12px;
                    border: 1px solid #C4C6CC;
                    cursor: pointer;
                    &.active {
                        color: #3A84FF;
                        background: #E1ECFF;
                        border: 1px solid #3A84FF;
                    }
                    &:first-child {
                        border-top-left-radius: 3px;
                        border-bottom-left-radius: 3px;
                    }
                    &:last-child {
                        border-left: none;
                        border-top-right-radius: 3px;
                        border-bottom-right-radius: 3px;
                    }
                }
            }
            span {
                color: #3A84FF;
                line-height: 25px;
                cursor: pointer;
            }
        }
        .list-container {
            color: #63656E;
            height: 260px;
            margin-top: 10px;
            overflow-y: hidden;
            ul {
                list-style: none;
                li {
                    display: flex;
                    margin-bottom: 10px;
                    padding: 5px;
                    box-sizing: border-box;
                    vertical-align: middle;
                    align-items: center;
                    >span {
                        border-radius: 2px;
                        height: 25px;
                        line-height: 25px;
                        padding: 0 10px;
                        &.success {
                            background: #FEEBEA;
                            color: #EA3536;
                        }
                        &.fail {
                            background: #E4FAF0;
                            color: #14A568;
                        }
                    }
                    .list-info {
                        flex: 1;
                        p {
                            font-size: 13px;
                            margin: 0 0 5px 0;
                            letter-spacing: 1px;
                        }
                        span {
                            color: #C4C6CC;
                            font-size: 12px;
                        }
                    }
                    &:hover {
                        background: #fafbfd;
                    }
                }
            }
            &::-webkit-scrollbar {
                /*滚动条整体样式*/
                width: 6px;  /*高宽分别对应横竖滚动条的尺寸*/
                height: 3px;
                border: none;
            }
            &::-webkit-scrollbar-thumb {
                /*滚动条里面小方块*/
                border-radius: 30px;
                box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
                background: rgba(186, 198, 204, .6);
            }
            &::-webkit-scrollbar-track {
                /*滚动条里面轨道*/
                border-radius: 30px;
                background: transparent;
            }
            &:hover {
                overflow-y: auto;
            }
            /deep/ .part-img {
                margin-top: 9.5%;
            }
        }
    }
    .ip-resource-pool {
        /deep/ .part-img {
            margin-top: 9%;
        }
    }
    .font-color-grey {
        color: #313238 !important;
        font-weight: 900;
        span {
            color: #63656E !important;
        }
    }
}
